@import "../../../../assets/scss/_base";
@import "../../../../assets/scss/_mixins";

.b-footer{
  padding: em(18) em(42);
  border-top: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  &__logo{
    margin-right: em(15);
  }
  &__copyrights{
    color: #333333;
    $font-size: 12;
    font-size: em($font-size);
    font-weight: 500;
  }
  &__created{
    display: flex;
    align-items: center;
    text-decoration: none;
  }
  &__social{
    a{
      width: 30px;
      height: 30px;
      display: inline-block;
      background: url(data:image/svg+xml;base64,) no-repeat 50% 50%;
      cursor: pointer;
      background-size: auto 25px;
    }
  }
}

@media (max-width: ($base__size_xs - 1) * 1px) {
  .b-footer{
    &__social{
      width: 100%;
      padding: 20px 0;
      text-align: center;
    }
  }
}