// =============================================================================
// String Replace
// =============================================================================

@function str-replace($string, $search, $replace: "") {
    $index: str-index($string, $search);

    @if $index {
        @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
    }

    @return $string;
}

// =============================================================================
// Font Face
// =============================================================================

@mixin font-face($name, $path, $weight, $style: null, $exts: eot woff2 woff ttf svg) {
    $src: null;

    $weightPrefix: "regular";

    @if $weight == 100 {
        $weightPrefix: "thin";
    } @else if $weight == 200 {
        $weightPrefix: "extra_light";
    } @else if $weight == 300 {
        $weightPrefix: "light";
    } @else if $weight == 500 {
        $weightPrefix: "medium";
    } @else if $weight == 600 {
        $weightPrefix: "semi_bold";
    } @else if $weight == 700 {
        $weightPrefix: "bold";
    } @else if $weight == 800 {
        $weightPrefix: "extra_bold";
    } @else if $weight == 900 {
        $weightPrefix: "black";
    } @else {
        $weightPrefix: "regular";
    }

    $extmods: (
        eot: "?",
        svg: "#" + str-replace($name, " ", "_")
    );

    $formats: (
        otf: "opentype",
        ttf: "truetype"
    );

    @each $ext in $exts {
        $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
        $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
        $src: append($src, url(quote($path + "-" + $weightPrefix + "." + $extmod)) format(quote($format)), comma);
    }

    @font-face {
        font-family: quote($name);
        font-style: $style;
        font-weight: $weight;
        src: $src;
    }
}

/*
    convert to em
*/
@function em($px, $base: $base__font-size){
    @return ($px / $base) *1em;
}
/*
    convert to rem
*/
@function rem($px){
    @return ($px / $base__font-size) *1rem;
}

/*grid*/
$cols: 12;
$gutter: 1.8367346938776%;

$one_col: (100% - ($gutter * ($cols - 1))) / $cols;

@mixin cols($num) {
    width: ($one_col * $num) + ($gutter * ($num - 1));
}
@mixin offset($num, $first) {
    @if $first == true{
        margin-left: ($one_col * $num) + ($gutter * $num);
    } @else {
        margin-left: ($one_col * $num) + ($gutter * ($num + 1));
    }

}