@import "_base";
@import "_mixins";
.b-iconpage {
  position: relative;
  &__title {
    color: #e52937;
    $font-size: 50;
    font-size: em($font-size);
    font-weight: 500;
    line-height: 1;
    margin-bottom: em(10, $font-size);
  }
  &__category {
    color: #e52937;
    $font-size: 14;
    font-size: $font-size * 1px;
    font-weight: 500;
    border-radius: $font-size * 1px;
    border: 1px solid #e52937;
    background-color: #ffffff;
    padding: em(7, $font-size) em(15, $font-size);
    margin-right: em(10, $font-size);
    text-decoration: none;
    vertical-align: top;
    display: inline-block;
  }
  &__tags {
    display: flex;
    margin-bottom: em(18);
    flex-wrap: wrap;
    li {
      color: #848d9c;
      $font-size: 14;
      font-size: em($font-size);
      font-weight: 500;
      border-radius: 6px;
      background-color: #f6f7f9;
      padding: em(4, $font-size) em(15, $font-size);
      min-width: em(110, $font-size);
      margin-right: em(7, $font-size);
      list-style: none;
      text-align: center;
      margin-bottom: em(7);
    }
  }
  &__description {
    margin-bottom: em(22);
    p {
      color: #2b2b2b;
      $font-size: 16;
      font-size: em($font-size);
      margin-bottom: em(22, $font-size);
    }
  }
  &__did-you-know {
    margin-bottom: em(40);
    p {
      color: #898989;
      $font-size: 14;
      font-size: em($font-size);
      font-weight: 500;
      margin-bottom: em(20, $font-size);
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__related {
    overflow: hidden;
    margin-bottom: 20px;
  }
  &__related-title {
    color: #333333;
    $font-size: 16;
    font-size: em($font-size);
    font-weight: 500;
  }
  &__related-icons {
    display: flex;
    flex-wrap: wrap;
    margin: 0 em(-12);
  }
  &__icon {
    width: 100%;
    padding-bottom: 100%;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: contain;
  }
  &__icon-wrap {
    margin-bottom: em(90);
  }
  &__sponsor {
    $font-size: 16;
    font-size: em($font-size);
    min-height: em(80, $font-size);
    padding: em(16, $font-size) em(115, $font-size) em(16, $font-size)
      em(28, $font-size);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    background: #ffffff;
    background-size: auto 80px;
    margin-bottom: em(20, $font-size);
    color: #2b2b2b;
    font-weight: 500;
    position: relative;
    &-icon {
      position: absolute;
      right: 20px;
      top: 0;
      height: 100%;
      width: em(85, $font-size);
      display: flex;
      align-items: center;
      justify-content: center;
    }
    img {
      display: block;
      max-width: 100%;
    }
  }
  &__share {
    margin-bottom: em(24);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    .react-share__ShareButton {
      font-size: $base__font-size * 1px;
      margin-right: em(10);
      &:last-child {
        margin-right: 0;
      }
      svg {
        display: block;
      }
    }
    &-text {
      color: #848d9c;
      $font-size: 16;
      font-size: em($font-size);
      display: inline-block;
      margin-right: 15px;
      font-weight: 700;
    }
    &-ctc {
      cursor: pointer;
      width: 40px;
      height: 40px;
      display: inline-block;
      margin-right: 15px;
      background: url(data:image/svg+xml;base64,)
        no-repeat 50% 50%;
    }
  }
  .fb-comments {
    margin-bottom: 30px;
  }
  &__nav {
    position: fixed;
    width: 100%;
    height: 0;
    top: 50%;
    left: 50%;
    max-width: 1025px;
    transform: translateX(-50%);
    z-index: 10;
  }
  &__nav-prev {
    width: 44px;
    height: 40px;
    position: absolute;
    top: 0;
    left: 0;
    border: 0;
    background: url(data:image/svg+xml;base64,)
      no-repeat 50% 50% #fff;
    box-sizing: border-box;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    transform: translate(-100%, -50%);
    border-radius: 10px;
    cursor: pointer;
    z-index: 10;
  }
  &__nav-next {
    width: 44px;
    height: 40px;
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    background: url(data:image/svg+xml;base64,)
      no-repeat 50% 50% #fff;
    box-sizing: border-box;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    transform: translate(100%, -50%);
    border-radius: 10px;
    cursor: pointer;
    z-index: 10;
  }
  &__translation {
    color: #e52937;
    $font-size: 24;
    font-size: em($font-size);
    font-weight: 500;
    padding: 0 0 0 43px;

    margin-bottom: em(33, $font-size);
  }
  &__categories {
    margin-bottom: em(20);
  }
  &__views {
    color: #848d9c;
    $font-size: 16;
    font-size: em($font-size);
    margin-bottom: em(20, $font-size);
    span {
      display: inline-block;
      margin-right: 15px;
    }
  }
}

@media (max-width: 1025px) {
  .b-iconpage {
    &__nav-prev {
      transform: translate(0, -50%);
    }
    &__nav-next {
      transform: translate(0, -50%);
    }
  }
}

@media (max-width: ($base__size_sm - 1) * 1px) {
  .b-iconpage {
    &__icon-wrap {
      max-width: em(400);
      margin: 0 auto em(30);
    }
    &__title {
      $font-size: 40;
      font-size: em($font-size);
      margin-bottom: em(20, $font-size);
      &-text {
        margin-bottom: em(14, $font-size);
        display: block;
      }
    }
    &__category {
      $font-size: 14;
      font-size: $font-size * 1px;
      border-radius: $font-size * 1px;
      padding: em(7, $font-size) em(15, $font-size);
      margin-right: em(10, $font-size);
      margin-left: 0;
    }
    &__description {
      margin-bottom: em(14);
      p {
        $font-size: 14;
        font-size: em($font-size);
      }
    }
    &__did-you-know {
      margin-bottom: em(20);
      p {
        $font-size: 12;
        font-size: em($font-size);
      }
    }
    &__sponsor {
      $font-size: 12;
      font-size: em($font-size);
      min-height: em(60, $font-size);
      font-weight: 500;
      padding: em(16, $font-size) em(115, $font-size) em(16, $font-size)
        em(28, $font-size);
      margin-bottom: em(20, $font-size);
    }
  }
}
